import SocialStrikeBack from "./SocialStrikeBack";
import ParallaxObjects from "./ParallaxObjects";

jQuery(document).ready(function(){
  if(window.innerWidth > 767) {
    new SocialStrikeBack();
    new ParallaxObjects();
  } else {
    document.getElementById('quotes-element').classList.add("is-appeared");
  }
});
/* eslint-enable */
