/* eslint-disable */

const HelperUtils = {
  getWindowScrollTop: () => window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0,
  getDocumentHeight: () => Math.max( document.body.scrollHeight, document.body.offsetHeight, document.documentElement.clientHeight, document.documentElement.scrollHeight, document.documentElement.offsetHeight )
};

class ParallaxFloat {

  /***
   *
   * @param { Element } el
   * @param { ParallaxObjects } root
   */
  constructor(el, root) {
    this.el = el;
    this.root = root;
    this.factor = el.getAttribute('data-parallax') || 1;
    this.initOffset = parseInt(el.getAttribute('data-parallax-offset')) || 0;
    this.transition = 'translate 0.1s ease';
    this.resize();
    this.update();
  }

  resize() {
    this.clearPositions();
    this.box = this.el.getBoundingClientRect();
    this.height = this.box.height;
    this.offset = this.box.top + HelperUtils.getWindowScrollTop();
  }

  clearPositions() {
    this.el.style.transform = 'unset';
    this.el.style.transition = 'unset';
  }

  calcTransform() {
    return Math.round(((this.offset - (this.root.clientHeight / 2) + this.height) - this.root.latestKnownScrollY) * this.factor + this.initOffset);
  }

  setDirection() {
    this.el.style['-webkit-transform'] = 'translateY(' + this.transform + 'px)';
    this.el.style['-moz-transform'] = 'translateY(' + this.transform + 'px)';
    this.el.style['transform'] = 'translateY(' + this.transform + 'px)';
    this.el.style['transition'] = this.transition;
    this.el.style['will-change'] = 'transform';
  }

  update() {
    this.transform = this.calcTransform();
    this.setDirection();
  }
}

class ParallaxObjects {
  constructor() {
    this.bindMethods();

    this.body = document.body;
    this.html = document.documentElement;
    this.window = window;
    this.ticking = false;
    this.latestKnownScrollY = HelperUtils.getWindowScrollTop();
    this.quotes = {
      trigger: document.getElementById('quotes-trigger'),
      element: document.getElementById('quotes-element'),
      isAppearing: false,
      boxTrigger: false,
      boxElement: false,
    };

    this.onResize();

    this.items = [];
    [... document.querySelectorAll('.parallax-float')].forEach(item => this.items.push(new ParallaxFloat(item, this)));

    this.initEvents();
    this.requestTick();

    setTimeout(() => {
      this.onResize();
    }, 1000);
  }

  onResize() {
    this.documentHeight = HelperUtils.getDocumentHeight();
    this.clientWidth = window.innerWidth;
    this.clientHeight = window.innerHeight;
    if(typeof this.items !== 'undefined' && this.items.length) {
      this.items.forEach( item => {
        item.resize();
        item.update();
      });
    }
  }

  onScroll() {
    this.latestKnownScrollY = HelperUtils.getWindowScrollTop();
    this.requestTick();
  }

  requestTick() {
    if (!this.ticking) {
      requestAnimationFrame(this.update);
      this.ticking = true;
    }
  }

  update() {
    if(typeof this.items !== 'undefined' && this.items.length) {
      this.items.forEach( item => item.update() );
    }

    if(!this.quotes.isAppearing) {
      this.quotes.boxTrigger = this.quotes.trigger.getBoundingClientRect();
      if((this.quotes.boxTrigger.top - this.quotes.boxTrigger.height) / this.quotes.boxTrigger.height < 0.25) {
        this.quotes.isAppearing = true;
        this.quotes.element.classList.add('is-appeared');
      }
    }

    this.ticking = false;
  }

  bindMethods() {
    ['onResize', 'update', 'onScroll']
      .forEach((fn) => this[fn] = this[fn].bind(this))
  }

  initEvents() {
    window.addEventListener('resize', this.onResize, {passive: true});
    window.addEventListener('scroll', this.onScroll, {passive: true});
  }
}

export default ParallaxObjects;

/* eslint-enable */
