const MathUtils = {
  // map number x from range [a, b] to [c, d]
  map: (x, a, b, c, d) => (x - a) * (d - c) / (b - a) + c,
  // linear interpolation
  lerp: (a, b, n) => (1 - n) * a + n * b
};

class Layer {
  constructor(x, y, w, h, sprite, z) {
    this.type = 'raster';
    this.x = x;
    this.y = y;
    this.width = w;
    this.height = h;
    this.sprite = sprite;
    this.z = z;
  }
}

class VectorLayer {
  constructor(x, y, w, h, object, z) {
    this.type = 'vector';
    this.x = x;
    this.y = y;
    this.width = w;
    this.height = h;
    this.object = object;
    this.z = z;
  }
}

class ParallaxItem {
  constructor(el, root, id) {
    this.id = id;
    this.root = root;
    this.DOM = {el: el};
    this.DOM.inner = this.DOM.el.querySelector('.ssb-article--inner');
    // this.isVisible = true;
    this.renderedStyles = {
      previous: 0,
      current: 0,
      ease: 1,
      maxValue: this.root.parallaxOverflow,
      minValue: -this.root.parallaxOverflow,
      setValue: () => {
        return Math.max(Math.min(MathUtils.map(this.props.top - this.root.latestKnownScrollY, this.root.winsize.height, -1 * this.props.height, this.renderedStyles.minValue, this.renderedStyles.maxValue), this.renderedStyles.maxValue), this.renderedStyles.minValue)
      }
    };
    this.update();
    this.observer = new IntersectionObserver((entries) => {
      entries.forEach(entry => this.isVisible = entry.intersectionRatio > 0);
    }, {rootMargin: '20px'});
    this.observer.observe(this.DOM.el);
    this.initEvents();
  }
  update() {
    this.getSize();
    this.renderedStyles.current = this.renderedStyles.previous = this.renderedStyles.setValue();
    this.layout();
  }
  getSize() {
    const rect = this.DOM.el.getBoundingClientRect();
    this.props = {
      height: rect.height,
      top: this.root.latestKnownScrollY + rect.top
    }
  }
  initEvents() {
    window.addEventListener('resize', () => this.resize());
  }
  resize() {
    this.update();
  }
  render() {
    this.renderedStyles.previous = this.renderedStyles.setValue();
    // this.renderedStyles.current = this.renderedStyles.setValue();
    // this.renderedStyles.previous = MathUtils.lerp(this.renderedStyles.previous, this.renderedStyles.current, this.renderedStyles.ease);
    this.layout();
  }
  layout() {
    this.DOM.inner.style.transform = `translate3d(0,${this.renderedStyles.previous}px,0)`;
  }
}

